import * as React from "react";
import { Grid, Box, Typography } from "@mui/material";
import {
  useTheme,
  // styled 
} from "@mui/material/styles";
// import AssignmentIcon from '@mui/icons-material/Assignment';
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { useStaticAssets } from "../../../hooks/useStaticAssets";

// const Image = styled(Box)({
//   width: "100%",
//   height: "auto",
//   objectFit: "cover"
// });

export default function PointOfInterest() {
  const theme = useTheme();
  const staticAssets = useStaticAssets();

  const items = [
    {
      img: { src: "/images/professionalita.jpg" },
      icon: <></>,
      label: "Professionalità",
      content:
        <article id="professionalità">I medici che fanno parte della nostra équipe sono affiatati, competenti ed
          <strong> estremamente preparati</strong>. Sono selezionati con cura tra i migliori professionisti del settore e si aggiornano costantemente per offrire sempre tecniche di intervento efficaci e predicibili, oltre che
          <strong> minimamente invasive</strong> ; collaborano fra loro per garantire ai pazienti la risoluzione di ogni problema odontoiatrico in modo specializzato, avendo ognuno le proprie aree di competenza ma trattando ogni paziente da un punto di vista multidisciplinare.
        </article>
    },
    {
      img: { src: "/images/tecnologia.jpg" },
      icon: <></>,
      label: "Tecnologia",
      content:
        <article id="tecnologia">L’ambulatorio Novident si fornisce costantemente di apparecchiature e strumentazioni sempre nuove e tecnologicamente all’avanguardia. Ad esempio per poter eseguire una corretta diagnosi, utilizziamo
          <strong> radiografie digitali con tecnologia ai fosfori</strong>,
          <strong> tac dentale</strong> (
          <em>cone beam</em>) di ultima generazione ed una
          <strong> telecamera intraorale</strong>. Inoltre, per regalare comfort ai pazienti e precisione tecnica ai clinici, siamo dotati di uno
          <strong> scanner ottico</strong> di altissimo livello per la rilevazione delle impronte dentali e di una macchina combinata ad ultrasuoni e polveri di ultimissima generazione a temperatura dell’acqua riscaldata per una seduta di ablazione del tartaro precisa ed accurata con un estremo comfort del paziente. Infine ci siamo dotati di diversi macchinari moderni e di alto livello per
          <strong> automatizzare i processi di disinfezione e sterilizzazione</strong> degli strumenti, potendo offrire sempre ai nostri pazienti il massimo dell’igiene verificabile e nessun rischio di contaminazione.
        </article>
    },
    {
      img: { src: "/images/attenzione-al-paziente.jpg" },
      icon: <></>,
      label: "Attenzione al paziente",
      content:
        <article id="">I professionisti (medici, igientisti, personale amministrativo ed assistenti odontoiatriche) che collaborano in Novident sono
          <strong> giovani e dinamici</strong>, ma soprattutto affiatati ed uniti, oltre che estremamente empatici verso i pazienti: qui troverete sempre un ambiente disteso e confortevole, dove qualsiasi figura cercherà nell’ambito del proprio ruolo e delle proprie competenze di seguirvi in modo preciso e soprattutto empatico, parola troppo spesso dimenticata del nostro ambiente. Da noi il
          <strong> benessere della persona</strong> nella sua interezza è il primo obiettivo,
          <strong> sempre</strong>.
        </article>
    },
  ];

  return (
    <Grid
      container
      spacing={1}
      sx={{
        backgroundColor: theme.palette.primary.soft,
        borderRadius: "1rem",
        py: 2
      }}
    >
      {items.map(({ img, icon, label, content }, n) => {
        const firstOrder = n * 2,
          secondOrder = firstOrder + 1,
          isEven = !(n % 2),
          relativePath = img.src.replace("/images/", "")
          ;
        // console.log(relativePath, staticAssets.images.generics);

        return (
          <React.Fragment key={n}>
            <Grid
              className="img-child"
              item
              order={{ xs: firstOrder, md: isEven ? firstOrder : secondOrder }}
              xs={12}
              md={6}
              sx={{
                position: "relative",
                minHeight: "250px",
                overflow: "hidden"
              }}
              style={{
                paddingLeft: "1rem",
                paddingRight: "1rem",
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center"
              }}
            >
              {/* <Image
                component="img"
                title={label}
                sx={{
                  position: "absolute",
                  maxWidth: "300px",
                  top: "50%",
                  left: "50%",
                  transform: "translate(-50%, -50%)",
                  [theme.breakpoints.up("md")]: {
                    left: isEven ? "90%" : "10%",
                    transform: isEven
                      ? "translate(-100%, -50%)"
                      : "translate(0%, -50%)"
                  }
                }}
                {...img}
              /> */}
              <Box
                sx={{
                  borderRadius: 1, // 16px rounded corners
                  boxShadow: 0, // Material-UI shadow level
                  overflow: 'hidden', // Ensure the image does not overflow the rounded corners
                  width: 500,
                }}
              ><GatsbyImage
                  alt={`${label} image`}
                  image={getImage(staticAssets.images.generics[relativePath])}
                />
              </Box>

            </Grid>
            <Grid
              className="text-child"
              item
              order={{ xs: secondOrder, md: isEven ? secondOrder : firstOrder }}
              xs={12}
              md={6}
              style={{
                paddingLeft: "1rem",
                paddingRight: "1rem",
                display: "flex",
                flexDirection: "column",
                justifyContent: "center"
              }}
            >
              {icon}
              <Typography variant="h6" sx={{ textAlign: "start" }}>
                {label}
              </Typography>
              <Typography textAlign="justify" component="div">{content}</Typography>
            </Grid>
          </React.Fragment>
        );
      })}
    </Grid>
  );
}
