import React, { useState, useEffect } from 'react';
import { Box } from '@mui/material';
import { styled } from '@mui/material/styles';
import NavBar from '../../../components/Header/Navbar';
import ContactUs from '../../../components/Buttons/ContactUs';

const Wrapper = styled(Box)(({ theme, bgPosition }) => ({
  background:
    // `linear-gradient(to top, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.25)),` +
    // `linear-gradient(to left, rgba(255, 255, 255, 0.175), rgba(255, 255, 255, 0.075), rgba(255, 255, 255, 0.175)),` +
    `url('/images/welcome-bg.jpg')`,
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'cover',
  backgroundPosition: bgPosition,
  borderRadius: '1rem',
  height: 'calc(100vh - 4rem)',
  minHeight: '400px',
}));

export default function Welcome() {
  const [bgPosition, setBgPosition] = useState('center center');

  useEffect(() => {
    function handleResize() {
      const widthThreshold1 = 784;
      const widthThreshold2 = 600;
      const newPosition = window.innerWidth <= widthThreshold2 ?
      `-${85 + (widthThreshold2 - window.innerWidth) / 10}px` : window.innerWidth <= widthThreshold1 ?
      `-85px` : 'center center';

      // Only update the state if necessary to avoid unnecessary re-renders
      if (newPosition !== bgPosition) {
        setBgPosition(newPosition);
      }
    }

    // Set initial position based on the current window size
    handleResize();

    // Add event listener for window resize
    window.addEventListener('resize', handleResize);

    // Remove event listener on cleanup
    return () => window.removeEventListener('resize', handleResize);
  }, [bgPosition]); // Include bgPosition in the dependency array to check against new position

  return (
    <Wrapper bgPosition={bgPosition}>
      <NavBar />
      <Box sx={{ position: 'absolute', top: '75%', left: '15%' }}>
        <ContactUs />
      </Box>
    </Wrapper>
  );
}
