import * as React from "react";
import { styled } from "@mui/material/styles";
import { useState } from "react";
import CommonButton from "../common/CommonButton/CommonButton";
import ContactsModal from "../Modals/ContactsModal";

const StyledButton = styled(CommonButton)(({ theme }) => ({
  backgroundColor: theme.palette.primary.main,
  paddingLeft: theme.spacing(2),
  paddingRight: theme.spacing(2),
  paddingTop: theme.spacing(1),
  paddingBottom: theme.spacing(1),
  borderRadius: theme.shape.borderRadius,
  "&:hover": {
    backgroundColor: theme.palette.primary.light
  },
  "& .MuiTouchRipple-root": {
    color: theme.palette.white.main
  }
}));

const TextLabel = styled("span")(({ theme }) => ({
  fontSize: theme.typography.pxToRem(23),
  color: theme.text.secondary.main,
  textTransform: "none",
  marginLeft: 0
}));

export default function ContactUs({ label, ...rest }) {
  const [modalOpened, setModalOpened] = useState(false);
  const handleClick = (evt) => {
    setModalOpened(true);
  };
  const handleModalClose = (evt) => {
    setModalOpened(false);
  };
  return (
    <>
      <StyledButton onClick={handleClick} {...rest}>
        <TextLabel>{label === undefined ? "Contattaci" : label}</TextLabel>
      </StyledButton>
      <ContactsModal open={modalOpened} onClose={handleModalClose} />
    </>
  );
}
