import * as React from "react";
import { Box } from "@mui/material";
import { useTheme } from "@mui/material/styles";
import BasicModal from "../common/BasicModal/BasicModal";
import {
  ContactEmailIconBaloon as MailTo,
  ContactPhoneIconBaloon as PhoneTo,
  ContactWhatsappIconBaloon as WhatsappTo
} from "../Buttons/contactButtons";
import { Client } from "../../globals";

export default function ContactsModal({ ...props }) {
  const sx = { px: 8, py: 0.5, mb: 1 };
  const theme = useTheme();
  const content = (
    <Box>
      <MailTo
        variant="primary"
        label="Email"
        size={2}
        justifyContent="space-between"
        sx={sx}
      />
      <PhoneTo
        variant="primary"
        label="Fisso"
        tel={Client.phone}
        size={2}
        justifyContent="space-between"
        sx={sx}
      />
      <PhoneTo
        variant="primary"
        label="Cellulare"
        tel={Client.mobile}
        size={2}
        justifyContent="space-between"
        sx={sx}
      />
      <WhatsappTo
        variant="primary"
        label="Whatsapp"
        size={2}
        justifyContent="space-between"
        sx={sx}
      />
    </Box>
  );
  return (
    <BasicModal
      title="Contattaci"
      content={content}
      wrapperSx={{ color: theme.text.white.main }}
      {...props}
    />
  );
}
